export const seriesConfigSchema = {
	type: 'object',
	properties: {
		axis: {type: 'string'},
		x: {type: 'string'},
		y: {type: 'string'}
	}
};

const tickConfigSchema = {
	type: 'object',
	properties: {
		position: {type: 'string', enum: ['center', 'before', 'after', 'inline']},
		fontFamily: {type: 'string'},
		fontStyle: {type: 'string'},
		fontWeight: {type: 'string'},
		fontSize: {type: 'string'},
		rotation: {type: 'number'},
		translation: {type: 'array', items: {type: 'number'}},
		tickFormat: {type: 'string'},
		tickPadding: {type: 'number'},
		tickSizeInner: {type: 'number'},
		tickSizeOuter: {type: 'number'},
		ticks: {type: ['number', 'object']},
		tickArguments: {type: 'array'},
		tickValues: {type: 'array', items: {type: 'number'}},
		disabled: {type: 'boolean'},
		labelColor: {type: 'string'},
		tickColor: {type: 'string'},
		strokeWidth: {type: 'number'},
		wrapLabels: {type: 'boolean'},
		truncationType: {type: 'string', enum: ['start', 'middle', 'end']},
		maximumSize: {type: 'number'}
	}
};

const titleConfigSchema = {
	type: 'object',
	properties: {
		fontFamily: {type: 'string'},
		fontSize: {type: 'string'},
		fontStyle: {type: 'string'},
		fontWeight: {type: 'string'},
		translation: {type: 'array', items: {type: 'number'}},
		padding: {type: 'number'},
		color: {type: 'string'},
		customLocation: {
			type: 'object',
			properties: {
				x: {type: 'number'},
				y: {type: 'number'},
				r: {type: 'number'},
				anchor: {type: 'string', enum: ['middle', 'start', 'end']}
			}
		}
	}
};

export const axisConfigSchema = {
	type: 'object',
	properties: {
		orientation: {type: 'string', enum: ['left', 'right', 'top', 'bottom']},
		tickConfig: tickConfigSchema,
		titleConfig: titleConfigSchema,
		translation: {type: 'array', items: {type: 'number'}},
		strokeWidth: {type: 'number'},
		title: {type: 'string'},
		autoRotateLabels: {type: 'boolean'}
	}
};

export const rangeLabelSchema = {
	type: 'object',
	properties: {
		format: {type: 'string'}
	}
};
