import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {
	buildAndSanitizePropsMaps,
	createXenoDomReadyHandler,
	applyPropToChartInstance,
	createXenoPropPreProcessMap
} from '@servicenow/library-chart';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import themeBehavior from '@servicenow/behavior-theme';
import {Navigator} from 'xenolith/dist/es';
import styles from './_now-chart-navigator.scss';
import {
	seriesConfigSchema,
	axisConfigSchema,
	rangeLabelSchema
} from './schemas/chart-navigator-schema';
import {t} from 'sn-translate';
import {configAriaSchema} from '@servicenow/library-schemas';
import {
	createTooltipActionHandler,
	schedule,
	unschedule
} from '@servicenow/now-tooltip/src/scheduler';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

import {actionTypes} from '@servicenow/ui-core';
const {
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED
} = actionTypes;

/** @seismicView now-chart-navigator */
const view = (state) => {
	const {content, targetRef, opened} = state.infoTooltip;
	/*
    Must 1) wait for the chartInstance / behavior to be instatiated
    2) only apply the theme if it is different or else it triggers a Xenolith render.
  */
	if (
		state.chartInstance &&
		state.behaviors.theme &&
		JSON.stringify(state.behaviors.theme) !==
			JSON.stringify(state.chartInstance.theme)
	) {
		state.chartInstance.theme = state.behaviors.theme;
	}

	return (
		<Fragment>
			<div id="chart-container" />
			<now-tooltip
				id="tooltip"
				manage-opened
				content={content}
				target-ref={targetRef}
				opened={opened}
				delay={{show: 0, hide: 0}}
			/>
		</Fragment>
	);
};

const PROPERTIES = {
	/**
	 * Specifies whether the chart is rendered in accessibility mode.
	 * @type {boolean}
	 */
	accessibilityMode: {
		xenolith: true,
		default: false,
		schema: {type: 'boolean'}
	},
	/**
	 * Base color to use for rendering the data series.
	 * @type {number}
	 */
	color: {
		xenolith: true,
		schema: {type: 'string'}
	},
	/**
	 * An object whose keys reference a specific HTML element within the
	 * now-chart-navigator. The value of each key is an object that contains
	 * an ARIA property (or properties) and corresponding value/s. These ARIA
	 * properties will be set on the specified element.
	 * The 'navigator' key corresponds to the inner html `<div>` element in
	 * now-chart-navigator that wraps the navigator. Use this property to
	 * supply an aria-label that describes the navigator for screen reader users. See
	 * https://www.w3.org/TR/wai-aria-1.1/ for other properties and accepted values.
	 * @type {{ 'aria-*': string }}
	 */
	configAria: {
		schema: {
			anyOf: [
				{
					type: 'object',
					properties: {
						navigator: configAriaSchema
					},
					additionalProperties: false
				},
				configAriaSchema
			]
		}
	},
	/**
	 * Common series configuration settings. When a series configuration is used for mutliple series,
	 * it can be defined here and referenced from other series configuration properties using the `for` keyword.
	 * This property is a map object containing the dataset name and the configuration for the series.
	 * @type {Object<string, SeriesConfig>}
	 */
	datasetConfig: {
		xenolith: true,
		schema: {type: 'object', patternProperties: {'.*': seriesConfigSchema}}
	},
	/**
	 * Fixed or dynamic extents, limiting the data values displayed on the chart to a specific range.
	 * To specify an extent, pass an object whose keys are the axis names and whose values are array
	 * tuples holding `[min, max]` values. By default, the range is calculated dynamically from the data,
	 * which corresponds to passing `dynamic` as both the min and max extent value for both x and y axes.
	 *
	 * For example:
	 * ```js
	 * extents: {
	 *   x: ['dynamic', 10000],
	 *   y: [0, 100]
	 * }
	 * ```
	 * @type {Object<string, Array<number | 'dynamic'>>}
	 */
	extents: {xenolith: true, schema: {type: 'object'}},
	/**
	 * Extra space around the chart body in addition to
	 * the space allocated for the axes.
	 * @type {{left: number, right: number, top: number, bottom: number}}
	 */
	extraMargin: {
		xenolith: true,
		schema: {
			type: 'object',
			properties: {
				left: {type: 'number'},
				right: {type: 'number'},
				top: {type: 'number'},
				bottom: {type: 'number'}
			}
		}
	},
	/**
	 * Configuration for the forecast series on the chart. This property is a map object
	 * containing the series name and the configuration for the series.
	 * @type {Object<string, SeriesConfig>}
	 */
	forecastConfig: {
		xenolith: true,
		schema: {
			type: 'object',
			patternProperties: {'.*': seriesConfigSchema}
		}
	},
	/**
	 * The array of data points defining the forecast plotted element on the chart.
	 * Works in conjunction with the `forecastConfig` property.
	 * @type {SeriesDataPoint[]}
	 */
	forecastData: {xenolith: true, schema: {type: 'array'}},
	/**
	 * The key used to display the forecast data.
	 * If not supplied, the first series listed in the `forecastConfig` is used.
	 * @type {string}
	 */
	forecastKey: {xenolith: true, schema: {type: 'string'}},
	/**
	 * The chart's height in pixels. Enter either the number of pixels, or the string `auto`
	 * to use the height of the parent DOM element.
	 * @type {'auto' | number}
	 */
	height: {
		xenolith: true,
		schema: {
			oneOf: [{const: 'auto'}, {type: 'number'}]
		}
	},
	/**
	 * Configure time and number formatting for different locales.
	 * Specify the locale using one of the following two-part `language-COUNTRY` IETF codes:
	 * `ar-AE`, `ar-BH`, `ar-DJ`, `ar-DZ`, `ar-EG`, `ar-EH`, `ar-ER`, `ar-IL`, `ar-IQ`, `ar-JO`, `ar-KM`, `ar-KW`, `ar-LB`, `ar-LY`, `ar-MA`,
	 * `ar-MR`, `ar-OM`, `ar-PS`, `ar-QA`, `ar-SA`, `ar-SD`, `ar-SO`, `ar-SS`, `ar-SY`, `ar-TD`, `ar-TN`, `ar-YE`,
	 * `ca-ES`, `cs-CZ`, `de-CH`, `de-DE`, `en-CA`, `en-GB`, `en-IN`, `en-US` (default), `es-ES`, `es-MX`, `fi-FI`, `fr-CA`, `fr-FR`,
	 * `he-IL`, `hu-HU`, `it-IT`, `ja-JP`, `ko-KR`, `mk-MK`, `nl-NL`, `pl-PL`, `pr-BR`, `ru-RU`, `sv-SE`, `uk-UA`, `zh-CN`
	 * @type {string}
	 */
	locale: {xenolith: true, schema: {type: 'string'}},
	/**
	 * Interior offset applied to the left side of the navigator.
	 * Setting this property lets you align the navigator with a timeseries chart.
	 * @type {number}
	 */
	navigatorLeft: {
		xenolith: true,
		schema: {type: 'number'}
	},
	/**
	 * The width of the interactive part of the Navigator.
	 * Setting this property lets you align the navigator with a timeseries chart.
	 * @type {number}
	 */
	navigatorWidth: {
		xenolith: true,
		schema: {type: 'number'}
	},
	/**
	 * Label that appears below the navigator and displays the selected range.
	 * @type {RangeLabelConfig}
	 */
	rangeLabelConfig: {
		xenolith: true,
		schema: {
			type: 'object',
			patternProperties: {'.*': rangeLabelSchema}
		}
	},
	/**
	 * Currently selected domain.
	 * @type {Object<string, Array<number | 'dynamic'>>}
	 */
	selectedDomain: {xenolith: true, schema: {type: 'object'}},
	/**
	 * Configuration for the main series on the chart. This property is a map object
	 * containing the series name and the configuration for the series.
	 * @type {Object<string, SeriesConfig>}
	 */
	seriesConfig: {
		xenolith: true,
		schema: {
			type: 'object',
			patternProperties: {'.*': seriesConfigSchema}
		}
	},
	/**
	 * The array of data points defining the main plotted element on the chart.
	 * Works in conjunction with the `seriesConfig` property.
	 * @type {SeriesDataPoint[]}
	 */
	seriesData: {xenolith: true, schema: {type: 'array'}},
	/**
	 * The series key to display on the navigator.
	 * If not supplied, the component uses the first series listed in the `seriesConfig` property.
	 * @type {string}
	 */
	seriesKey: {xenolith: true, schema: {type: 'string'}},
	/**
	 * Determines how often the `selectedDomain` property updates in milliseconds.
	 * By default, the property updates every 40 milliseconds. More frequent updates
	 * may provide smoother transitions, but can impact performance.
	 * @type {number}
	 */
	throttleTiming: {xenolith: true, schema: {type: 'number'}},
	/**
	 * The chart's width in pixels. Enter either the number of pixels, or the string `auto`
	 * to use the width of the parent DOM element.
	 * @type {'auto' | number}
	 */
	width: {
		xenolith: true,
		schema: {
			oneOf: [{const: 'auto'}, {type: 'number'}]
		}
	},
	/**
	 * Configuration for the x axis.
	 * @type {AxisConfig}
	 */
	xAxisConfig: {xenolith: true, schema: axisConfigSchema},
	/**
	 * Type of scale used with the x axis.
	 * For descriptions of each type, see [d3 scales](https://github.com/d3/d3-scale).
	 * @type {('time'|'timeLocal')}
	 */
	xAxisType: {
		xenolith: true,
		schema: {
			type: 'string',
			enum: ['linear', 'time', 'timeLocal', 'log', 'band', 'point']
		}
	},
	/**
	 * Type of scale used with the y axis.
	 * For descriptions of each type, see [d3 scales](https://github.com/d3/d3-scale).
	 * @type {('linear'|'time'|'timeLocal'|'log'|'band'|'point')}
	 */
	yAxisType: {
		xenolith: true,
		schema: {
			type: 'string',
			enum: ['linear', 'time', 'timeLocal', 'log', 'band', 'point']
		}
	}
};

const {XENOLITH_PROPERTIES} = buildAndSanitizePropsMaps(PROPERTIES);
const preProcessMap = createXenoPropPreProcessMap();
// set up translation for aria-label on rangeLabel reset button
preProcessMap.rangeLabelConfig = {
	processArgs: t('reset selection'),
	process: function({value}, iconLabel) {
		return {...value, iconLabel};
	}
};
preProcessMap.configAria.processArgs = {
	key: 'navigator'
};

// array of properties to observe, where each element has the `xenolith` name, and reactions is an array of steps to take when the observed
// property changes. Reactions are processed in order. Can be one of three types:
//   dispatch: dispatch an action, args -> `type` and optional `processValue` function. payload has the (optionally processed) updated value.
//   updateState: update this components state. args -> `operation`, `path` and optional `processValue` function. component of state is updated with (optionally processed) value
//   updateProperties: update this components property. args -> `operation`, `propName` and optional `processValue` function. component of state is updated with (optionally processed) value
const OBSERVED_PROPERTIES = [
	{
		xenolith: 'selectedDomain',
		reactions: [
			{
				type: 'dispatch',
				processValue: (v) => ({value: v}),
				args: {type: 'NOW_CHART_NAVIGATOR#SELECTED_DOMAIN_CHANGED'}
			}
		]
	},
	{
		xenolith: 'requestTooltip',
		reactions: [
			{
				// Cannot use updateState because the tooltip need to use schedule.
				type: 'dispatch',
				args: {type: 'PRIVATE#TOOLTIP_REQUEST'}
			}
		]
	}
];

const showTooltip = (updateState, {payload, stopPropagation}) => {
	let value;
	stopPropagation();

	if (!payload) {
		value = {content: '', opened: false};
	} else {
		const {targetRef, textContent} = payload;
		value = {
			targetRef,
			content: textContent,
			opened: true
		};
	}

	updateState({
		path: 'infoTooltip',
		operation: 'set',
		value
	});
};

/**
 * Navigator is a small chart below or above a main chart that displays a view of the entire data set.
 * It can also be used by itself to show the data set for a layout, such as a dashboard or landing page.
 * Users can zoom in on and pan across the dataset.
 *
 * ```jsx
 * const options = {
 *   width: "auto",
 *   height: "auto",
 *   seriesConfig: {
 *     foo: { x: "timestamp", y: "y0" }
 *   },
 *   seriesData: [
 * 	   {timestamp: 1226766192448, y0: 5},
 *     ...otherData
 *   ]
 * }
 * <now-chart-navigator {...options} />
 * ```
 *
 * @seismicElement now-chart-navigator
 * @summary A small chart that displays an entire data set.
 */
createEnhancedElement('now-chart-navigator', {
	properties: PROPERTIES,
	initialState: {
		infoTooltip: {targetRef: null, content: '', opened: false}
	},
	behaviors: [
		{
			behavior: themeBehavior,
			options: {
				items: ['type.fontFamily', 'global.*', 'datavis.*']
			}
		}
	],
	actionHandlers: {
		...createTooltipActionHandler((updateState) => {
			updateState({
				path: 'infoTooltip',
				operation: 'set',
				value: {ref: null, content: '', opened: false}
			});
		}),
		[COMPONENT_DOM_READY]: createXenoDomReadyHandler(
			{
				xenoPropList: XENOLITH_PROPERTIES,
				preProcessMap,
				observedProps: OBSERVED_PROPERTIES
			},
			({host, state}) => {
				const chartContainer = host.shadowRoot.getElementById(
					'chart-container'
				);
				return new Navigator({
					node: chartContainer,
					theme: state.behaviors.theme
				});
			}
		),
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			state,
			dispatch,
			updateState,
			host,
			properties
		}) => {
			const {name, value} = action.payload;
			// we need to treat selectedDomain specially - Xenolith Navigator sets the brush domain, which triggers a selectedDomain change.
			if (name === 'selectedDomain') {
				state.chartInstance.setSelection(value.x);
			} else {
				applyPropToChartInstance({
					chartInstance: state.chartInstance,
					value,
					name,
					xenoPropList: XENOLITH_PROPERTIES,
					dispatch,
					preProcessMap: [],
					state,
					updateState,
					host,
					properties
				});
			}
		},
		[COMPONENT_DISCONNECTED]: ({action, state}) => {
			unschedule(action.meta.id);
			state.chartInstance.destroy();
		},
		'PRIVATE#TOOLTIP_REQUEST': {
			stopPropagation: true,
			effect: ({action, updateState}) => {
				schedule(action.meta.id, showTooltip.bind(null, updateState, action));
			}
		}
	},
	styles,
	view,
	dispatches: {
		/**
		 * Dispatched when the user interacts with the navigator and changes the `selectedDomain` property.
		 * @type {{value: { x: number[] }}}
		 */
		'NOW_CHART_NAVIGATOR#SELECTED_DOMAIN_CHANGED': {}
	}
});
